import AccountTypeService from '@/services/account/AccountTypeService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let AccountTypeTableMixin = {
  mixins: [LoadingMixin, ListLayoutMixin],
  props: {
    defaultAccountTypeTableFilter: {
      type: String,
      default: null
    },
    defaultAccountTypeTableOrderBy: {
      type: String,
      default: null
    },
    typeBehaviourEnumId: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      accountTypeTableData: [],
      accountTypeTableFilterTimer: null,
      accountTypeTableFilter: this.defaultAccountTypeTableFilter,
      accountTypeTableOrderBy: this.defaultAccountTypeTableOrderBy,
      accountTypeTableContainer: 'loading-container',
      accountTypeTableBusy: false,
      accountTypeTableTotalRows: 0,
      accountTypeTableCurrentPage: 1,
      accountTypeTablePerPage: 10
    }
  },
  computed: {
    accountTypeTableNoDataText: function () {
      return this.accountTypeTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    accountTypeTableTotalPages: function () {
      return Math.ceil(this.accountTypeTableTotalRows / this.accountTypeTablePerPage)
    },
    accountTypeTablePagingInfo: function () {
      return this.pagingInfo(this.accountTypeTableCurrentPage, this.accountTypeTablePerPage, this.accountTypeTableTotalRows)
    }
  },
  methods: {
    loadAccountTypeTableData (loadingContainerId) {
      let self = this
      this.accountTypeTableBusy = true
      this.accountTypeTableContainer = loadingContainerId || this.accountTypeTableContainer
      this.startLoading(this.accountTypeTableContainer)
      AccountTypeService.accountTypes({
        pageIndex: this.accountTypeTableCurrentPage - 1,
        pageSize: this.accountTypeTablePerPage,
        filter: this.accountTypeTableFilter,
        orderByField: this.accountTypeTableOrderBy,
        typeBehaviourEnumId: this.typeBehaviourEnumId
      }).then(function (response) {
        self.stopLoading(self.accountTypeTableContainer)
        self.accountTypeTableBusy = false
        self.accountTypeTableTotalRows = response.data.totalRows
        self.accountTypeTableData = response.data.accountTypeList || []
      }).catch(function (error) {
        self.stopLoading(self.accountTypeTableContainer)
        self.accountTypeTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshAccountTypeTable () {
      this.loadAccountTypeTableData()
    },
    filterAccountTypeTable (filter) {
      let self = this
      if (this.accountTypeTableFilter !== filter) {
        clearTimeout(this.accountTypeTableFilterTimer)
        this.accountTypeTableFilterTimer = setTimeout(() => {
          self.accountTypeTableFilter = filter
          self.loadAccountTypeTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.accountTypeTableFilterTimer)
      }
    },
    orderAccountTypeTable (orderBy, active) {
      this.accountTypeTableOrderBy = (active ? '' : '-') + orderBy
      this.loadAccountTypeTableData()
    }
  },
  watch: {
    accountTypeTableCurrentPage: function () {
      this.refreshAccountTypeTable()
    },
    accountTypeTablePerPage: function () {
      this.refreshAccountTypeTable()
    },
    typeBehaviourEnumId: function () {
      this.refreshAccountTypeTable()
    }
  }
}

export default AccountTypeTableMixin
